<template>
  <div>
    <div class="nav-div">
      <div class="navbar">
        <h3 class="sign-in" @click="isSignUp = false">Sign In</h3>
        <h3 class="sign-up" @click="isSignUp = true">Sign Up</h3>
      </div>
    </div>
    <!-- Navbar -->
    <div class="parent-div">
      <div class="columns is-centered is-vcentered" style="min-height: 100%">
        <div class="column is-half">
          <form @submit.prevent="submitForm">
            <b-field class="inp" label="Username" label-position="on-border">
              <b-input
                value="ex: johnsilver"
                v-model="username"
                maxlength="10"
                minlength="3"
                required
              >
              </b-input>
            </b-field>
            <b-field class="inp" label="Password" label-position="on-border">
              <b-input
                type="password"
                v-model="password"
                maxlength="10"
                minlength="5"
                required
                password-reveal
              >
              </b-input>
            </b-field>
            <b-field
              v-if="isSignUp"
              class="inp"
              label="Confirm Password"
              label-position="on-border"
            >
              <b-input
                type="password"
                v-model="confPassword"
                maxlength="10"
                minlength="5"
                required
                password-reveal
              >
              </b-input>
            </b-field>
            <!-- Button -->
            <div class="btn">
              <b-button
                class="action-btn"
                type="is-primary"
                native-type="submit"
              >
                {{ isSignUp ? "Sign Up" : "Sign In" }}
              </b-button>
            </div>
          </form>
        </div>
      </div>
      <div v-if="errorMessage" class="alert">
        <b-message type="is-danger">
          {{ errorMessage }}
        </b-message>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      username: null,
      password: null,
      confPassword: null,
      errorMessage: null,
      isSignUp: false,
    };
  },
  methods: {
    submitForm() {
      this.errorMessage = null;
      if (this.password !== this.confPassword && this.isSignUp)
        return (this.errorMessage = "Passwords Do not match!!");
      if (this.isSignUp) return this.signUp();
      else return this.signIn();
    },
    async signUp() {
      try {
        const { data } = await axios.post("/api/users/signup", {
          userName: this.username,
          password: this.password,
        });
        localStorage.setItem("userName", data.userName);
        localStorage.setItem("token", data.token);
        this.$emit("verify");
      } catch (err) {
        this.errorMessage = "User Already Exist!!";
      }
    },
    async signIn() {
      this.errorMessage = null;
      try {
        let { data } = await axios.post("/api/users/login", {
          userName: this.username,
          password: this.password,
        });
        console.log(data);
        localStorage.setItem("userName", data.userName);
        localStorage.setItem("token", data.token);
        this.$emit("verify");
      } catch (err) {
        this.errorMessage = "Invalid Credential !!";
      }
    },
  },
};
</script>

